import { useState, useRef } from "react";
import Input from "../../UI/Input";
import classes from "./MealItemForm.module.css";

const MealItemForm = (props) => {
  const amountRef = useRef();
  const [amountIsValid, setAmountIsValid] = useState(true);
  const submitHandler = (e) => {
    e.preventDefault();
    const enteredInput = amountRef.current.value;
    const enteredInputNumber = +enteredInput;
    if (
      enteredInput.trim().length === 0 ||
      enteredInputNumber > 5 ||
      enteredInputNumber < 1
    ) {
      setAmountIsValid(false);
      return;
    }
    props.onSubmit(enteredInputNumber);
  };
  return (
    <form className={classes.form} onSubmit={submitHandler}>
      <Input
        label={"amount"}
        ref={amountRef}
        input={{
          type: "number",
          id: "amount_" + props.id,
          step: "1",
          min: "1",
          max: "5",
          defaultValue: "1",
        }}
      />
      <button type="submit"> + Add</button>
      {!amountIsValid && <p>Please set a number between 1 and 5</p>}
    </form>
  );
};

export default MealItemForm;
