import { useReducer } from "react";
import CartContext from "./cart-context";

const defaultState = {
  items: [],
  totalAmount: 0,
};

const cartReducer = (state, action) => {
  if (action.type === "ADD") {
    const updatedAmount =
      state.totalAmount + action.item.price * action.item.amount;

    console.log(updatedAmount);
    const itemIndex = state.items.findIndex(
      (item) => item.id === action.item.id
    );

    const existingItem = state.items[itemIndex];

    let updatedItems;

    if (existingItem) {
      const newItem = {
        ...existingItem,
        amount: existingItem.amount + action.item.amount,
      };

      updatedItems = [...state.items];
      updatedItems[itemIndex] = newItem;
    } else {
      updatedItems = state.items.concat(action.item);
    }

    return {
      items: updatedItems,
      totalAmount: updatedAmount,
    };
  }
  if (action.type === "REMOVE") {
    const realIndexItem = state.items.findIndex(
      (item) => item.id === action.id
    );

    const existingItem = state.items[realIndexItem];
    const updatedAmount = state.totalAmount - existingItem.price;
    let updatedItems;
    if (existingItem.amount === 1) {
      updatedItems = state.items.filter((item) => item.id !== existingItem.id);
    } else {
      const newItem = {
        ...existingItem,
        amount: existingItem.amount - 1,
      };
      updatedItems = [...state.items];
      updatedItems[realIndexItem] = newItem;
    }
    return {
      items: updatedItems,
      totalAmount: updatedAmount,
    };
  }

  return defaultState;
};
const CartProvider = (props) => {
  const [cartState, cartDispatch] = useReducer(cartReducer, defaultState);

  const addCartItemHandler = (item) => {
    cartDispatch({ type: "ADD", item: item });
  };

  const removeCartItemHandler = (id) => {
    cartDispatch({ type: "REMOVE", id: id });
  };

  const cartContext = {
    items: cartState.items,
    totalAmount: cartState.totalAmount,
    addItem: addCartItemHandler,
    removeItem: removeCartItemHandler,
  };

  return (
    <CartContext.Provider value={cartContext}>
      {props.children}
    </CartContext.Provider>
  );
};

export default CartProvider;
